.pumpingStation{
  width: 411px;
  height: 636px;
  .yunxing{
    width: 137px;
    text-align: center;
    height: 37px;
    // border-image: url(../../assets/imgs/标题框.png) 0 0 0 0 fill round;
    p{
      font-size: 16px;
      line-height: 37px;
      letter-spacing: 1px;
      color: #ffffff;
      text-shadow: 0 0 30px rgb(244, 250, 245);
    }
  }
  .drainagePump{
    width: 411px;
    height: 589px;
    // border-image: url(../../assets/imgs/框.png) 1 0 0 0 fill round;
    // border-image-repeat: stretch;
    margin-top: 29px;
    overflow: hidden;
    .bzbeijin{
      display: inline-block;
      margin-top: 49px;
      margin-left: 18px;
      width: 392px;
      height: 199px;
      .psone{
        width: 182px;
        height: 45px;
        // background-image: url(../../assets/imgs/泵站.png);
        font-size: 16px;
        color: #fff;
        text-align: center;
        letter-spacing: 1px;
        line-height: 45px;
      }
      .condition{
        margin-left: 45px;
        width: 290px;
        height: 36px;
        p{
          float: left;
          display:inline-block;
          height: 36px;
          line-height: 36px;
          font-size: 14px;
          color: #00ffff;
        }
        .stop{
          margin-left: 22px;
              float: left;
              width: 17px;
              height: 33px;
          span{
            display:inline-block;
            width: 17px;
            height: 22px;
            // background-image: url(../../assets/imgs/状态-运行.png);
       
          }
          i{
            font-size: 12px;
            font-style: normal;
            color: #0090ff;
          }
        }   
      }

      // 进度条
      .jindu{
        margin-top: 17px;
        margin-left: 45px;
        height: 14px;
        .shunshi{
          width: 60px;
          height: 14px;
          float: left;
          font-size: 14px;
          color: #00ffff;
          letter-spacing: 1px;
        }
        .Time{
          font-size: 16px;
          color: #00ffff;
          span{
            font-size: 12px;
          }
        }
      }
      .container {
        // margin-top: 20px;
        margin-left: 20px;
        margin-right: 5px;
        float: left;
         width:156px;
        } 
      .title {
        margin-left: 20px;
         background:#545965; 
         color:#fff; 
         padding:15px; 
         float:left;
        position:relative;
         -webkit-border-top-left-radius:5px; 
         -webkit-border-bottom-left-radius:5px; 
         -moz-border-radius-topleft:5px; 
         -moz-border-radius-bottomleft:5px; 
         border-top-left-radius:5px; 
         border-bottom-left-radius:5px 
        }
      .bar { 
        height:18px; 
        display:block; 
        background:#0647bb;
        width:100%;
        border-radius:8px 
      }
      .bar1 {
         height:18px;
          display:block;
          background:#00ffff;
          width:0;
          border-radius:8px;
          -webkit-transition:width .8s ease;
          -moz-transition:width .8s ease;
          transition:width .8s ease;
          -webkit-animation:progressbar 7s infinite;
          animation:progressbar 7s infinite }
      @-webkit-keyframes progressbar { from { width:0 } to { width:60% } } /* Standard syntax */ @keyframes progressbar { from { width:0 } to { width:60% } }
    }
  }
}